// @dynamic
export class MdsStringUtils {
    static lowerCase = (str: string) => {
        return str.toLowerCase();
    }
    static upperCase = (str: string) => {
        return str.toUpperCase();
    }
    static titleCase = (str: string) => {
        return str.replace(/\b(\w)/g, (k: string) => k.toUpperCase());
    }
    static pascalCase = (str: string) => {
        return str.toLowerCase().replace(/[^\w\s]/gi, ' ').replace(/(?:_| |\b)(\w)/g, (p1: string) => {
        return p1.toUpperCase(); }).replace(/\s/g, '');
    }
    static randomCharacter = (length: number, characters?: string) => {
        // sample random character or custom characters
        const chars = characters ? characters : 'abcefghijklnopqrtuvwxyz0123456789';
        let result = '';
        for (let i = length; i > 0; --i) { result += chars[Math.floor(Math.random() * chars.length)]; }
        return result;
    }
    static convertToSlug = (text: string) => {
        if (!text) { return; }
        const result = text.toLowerCase().replace('.', ' ').replace('-', ' ').replace(/[^\w ]+/g, '').replace(/ +/g, '-');
        return result;
    }
    static getYoutubeID = (url: string) => {
        const regExp = /^.*(youtu\.be\/|v\/|u\/\w\/|embed\/|watch\?v=|&v=)([^#&?]*).*/;
        const match = url.match(regExp);
        if (match && match[2].length === 11) { return match[2]; }
        else { return ''; }
    }
    static getYoutubeEmbed = (url: string) => {
        const regExp = /^.*(youtu\.be\/|v\/|u\/\w\/|embed\/|watch\?v=|&v=)([^#&?]*).*/;
        const match = url.match(regExp);
        let id: string;
        if (match && match[2].length === 11) { id = match[2]; }
        return 'https://www.youtube.com/embed/' + id;
    }

    /* Size option
    // 0/1/2/3/default/sd1/sd2/sd3/sddefault/mq1/mq2/mq3/mqdefault/
    // hq1/hq2/hq3/hqdefault/hq720/maxres1/maxres2/maxres3/maxresdefault
    */
    static getYoutubeThumbnail = (url: string, size?: string) => {
        const regExp = /^.*(youtu\.be\/|v\/|u\/\w\/|embed\/|watch\?v=|&v=)([^#&?]*).*/;
        const match = url.match(regExp);
        let id: string;
        if (match && match[2].length === 11) { id = match[2]; }
        size = size ? size : 'hqdefault';
        return 'https://i.ytimg.com/vi/' + id + '/' + size + '.jpg';
    }
}
