import { AfterViewInit, Component, ElementRef, Input, OnChanges, ViewChild } from '@angular/core';
import * as Prism from 'prismjs';
import 'prismjs';
import 'prismjs/components/prism-markup';
import 'prismjs/components/prism-javascript';
import 'prismjs/components/prism-java';
import 'prismjs/components/prism-typescript';
import 'prismjs/components/prism-css';
import 'prismjs/components/prism-sass';
import 'prismjs/components/prism-scss';
import 'prismjs/components/prism-ruby';
import 'prismjs/components/prism-swift';
import 'prismjs/components/prism-sql';
import 'prismjs/components/prism-python';

@Component({
  selector: 'mds-hightlight-prism',
  templateUrl: './mds-hightlight-prism.component.html',
  styleUrls: ['./mds-hightlight-prism.component.scss']
})
export class MdsHightlightPrismComponent implements AfterViewInit, OnChanges {
  @ViewChild('codeContent') codeContent!: ElementRef;
  @Input() code?: string;
  @Input() language?: string;
  constructor() { }
  ngAfterViewInit(): any {
    Prism.highlightElement(this.codeContent.nativeElement);
  }
  ngOnChanges(changes: any): void {
    if (changes?.code) {
      if (this.codeContent?.nativeElement) {
        this.codeContent.nativeElement.textContent = this.code;
        Prism.highlightElement(this.codeContent.nativeElement);
      }
    }
  }
}
