import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { SelectedFilterInterface } from '../mds-filter.interface';

@Component({
  selector: 'mds-filter-reset',
  templateUrl: './filter-reset.component.html',
  styleUrls: ['./filter-reset.component.css']
})
export class MdsFilterResetComponent implements OnInit {
  @Input() content: any;
  @Input() filterSelected: SelectedFilterInterface;
  @Output() filterSelectedChange = new EventEmitter<any>();

  constructor(
    private router: Router
  ) { }

  ngOnInit(): void {
  }

  reset(): void{
    this.filterSelectedChange.emit({});
    this.router.navigate([]);
  }

}
