// @dynamic
export class MdsDateUtils {
    static untilNow(date: number, customdata?: string[], customprefix?: string): string {
        const intervals = [
            { label: 'year', seconds: 31536000 },
            { label: 'month', seconds: 2592000 },
            { label: 'day', seconds: 86400 },
            { label: 'hour', seconds: 3600 },
            { label: 'minute', seconds: 60 },
            { label: 'second', seconds: 1 },
        ];
        let prefix = 'ago';
        if (customprefix) { prefix = customprefix; }
        if (customdata) { customdata.forEach( (item, i) => intervals[i].label = item ); }
        const seconds = Math.floor((Date.now() - date) / 1000);
        const interval = intervals.find((i) => i.seconds < seconds);
        const count = Math.floor(seconds / interval.seconds);
        return `${count} ${interval.label}${count !== 1 && !customdata ? 's' : ''} ${prefix}`;
    }
}
