// @dynamic
export class MdsArrayUtils {
    static countUniqueValues = (data: any) => {
        const count: any = {};
        data.forEach((i: any) => {
            count[i] = (count[i] || 0) + 1;
        });
        return count;
    }
    static filterMultiple = (data: any[], key: string, filterdata: any[]) => {
        // console.log('FilterMultiple', data, key, filterdata)
        filterdata = filterdata.join().toLocaleLowerCase().split(',');
        const newdata: any = [];
        data.filter( (item: any) => {
            if (item[key]){
                item[key] = item[key].includes(',') ? item[key].split(',') : item[key];
                const isArray = Array.isArray(item[key]);
                // If values of key is string
                if (!isArray){
                    if (filterdata.indexOf(item[key].toLocaleLowerCase()) !== -1) {
                        newdata.push(item);
                    }
                }
                // If values of key is Array
                if (isArray){
                    item[key].forEach( (val: string) => {
                        if (filterdata.indexOf(val.toLocaleLowerCase()) !== -1) {
                            newdata.push(item);
                        }
                    });
                }
            }
        });
        return [...new Set(newdata)];
    }
    static groupValues = (arr: any[], key: string) => {
        const keyvalue = (a: any) => a[key];
        return arr.reduce((r: any, v: any, i: any, a: any, k: any = keyvalue(v)) => ((r[k] || (r[k] = [])).push(v), r), {});
    }
    static sumValues = (arr: any) => {
        return arr.reduce( (total: any, num: any) => total + num );
    }
    static arrayTolistObject = (arr: any, objectkey: string) => {
        const groupObj = {};
        for (const item of arr){
            const newobj: any = {};
            newobj[item[objectkey]] = item;
            Object.assign(groupObj, newobj);
        }
        return groupObj;
    }
}
