// @dynamic
export class MdsNumberUtils {
    static shortNumber = (value: number, custom?: string[]) => {
        const defaultdata = ['k', 'm', 'b', 't'];
        const suffixes = custom ? ['', ...custom] : ['', ...defaultdata];
        const suffixNum = Math.floor(('' + value).length / 3);
        let shortValue: any = parseFloat((suffixNum !== 0 ? (value / Math.pow(1000, suffixNum)) : value).toPrecision(2));
        if (shortValue % 1 !== 0) { shortValue = shortValue.toFixed(1); }
        return shortValue + suffixes[suffixNum];
    }
    static toCurrency = (money: number, code: string, decimal?: number) => {
        const formatlist = { IDR: 'id-ID', JYP: 'ja-JP', EUR: 'de-DE', USD: 'en-US' };
        const format = formatlist[code];
        decimal = decimal ? decimal : 0;
        return new Intl.NumberFormat(format,
            { style: 'currency', currency: code, minimumFractionDigits: decimal }
        ).format(money);
    }
}
